export default {
	data() {
		return {
			switchB: true,
			user:{}
		};
	},
	onLoad() {
		let _this=this
		uni.getStorage({
		key: 'ddwb',
		success: function (res) { 
			_this.user= {			
				name: res.data.name || '',
				phone:res.data.phone||'',
				headPortrait:res.data.headPortrait
			}
		}
		});
	},
	methods: {
		toRealInformmation(){
			uni.navigateTo({
			    url: 'realInformation'
			});
		},
		changeSwitch(e) {
			this.switchB = e.detail.value
		}
	}
}
